import React from "react";
import "./mobile-nav.styles.scss";
import { NavLink } from "react-router-dom";
import { connect } from "react-redux";
import { animated, useTransition } from "react-spring";
import { displayMobileNav } from "../../redux/nav/nav.actions";

const MobileNav = ({ toggleMobileNav, displayMobileNav }) => {
  const slideRight = useTransition(displayMobileNav, null, {
    from: { transform: "translateX(+100%)" },
    enter: { transform: "translateX(0%)" },
    leave: { transform: "translateX(+100%)" }
  });

  return (
    <nav className="mobile-nav-container">
      {slideRight.map(
        // props needed for reactSpring library
        ({ item, key, props }) =>
          item && (
            // animated.div className in order to keep css grid context
            <animated.div
              className="mobile-nav-container"
              key={key}
              style={props}
            >
              <ul className="mobile-nav-ul">
                <NavLink
                  exact
                  to="/sebtons-luxury-rentals/home"
                  className="mobile-nav-item"
                  onClick={toggleMobileNav}
                  activeClassName="mobile-is-current"
                >
                  Home
                </NavLink>
                <NavLink
                  exact
                  to="/sebtons-luxury-rentals/properties"
                  className="mobile-nav-item"
                  onClick={toggleMobileNav}
                  activeClassName="mobile-is-current"
                >
                  Properties
                </NavLink>
                <NavLink
                  exact
                  to="/sebtons-luxury-rentals/services"
                  className="mobile-nav-item"
                  onClick={toggleMobileNav}
                  activeClassName="mobile-is-current"
                >
                  Services
                </NavLink>
                <NavLink
                  exact
                  to="/sebtons-luxury-rentals/rentalpolicy"
                  className="mobile-nav-item"
                  onClick={toggleMobileNav}
                  activeClassName="mobile-is-current"
                >
                  Rental Policy
                </NavLink>
                <NavLink
                  exact
                  to="/sebtons-luxury-rentals/aboutus"
                  className="mobile-nav-item"
                  onClick={toggleMobileNav}
                  activeClassName="mobile-is-current"
                >
                  About Us
                </NavLink>
                <NavLink
                  exact
                  to="/sebtons-luxury-rentals/contact"
                  className="mobile-nav-item"
                  onClick={toggleMobileNav}
                  activeClassName="mobile-is-current"
                >
                  Contact
                </NavLink>
              </ul>
            </animated.div>
          )
      )}
    </nav>
  );
};

const mapDispatchToProps = dispatch => ({
  toggleMobileNav: () => dispatch(displayMobileNav())
});

const mapStateToProps = state => ({
  displayMobileNav: state.nav.displayMobileNav
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MobileNav);
